import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ManageUpcomingAndHistoricalPaymentsNavigationService } from '../../services/navigation.service';

@Component({
  selector: 'bb-upcoming-and-history-payments-tab-container-view',
  templateUrl: 'upcoming-and-history-payments-tab-container-view.component.html',
})
export class UpcomingAndHistoryPaymentsTabContainerViewComponent implements OnInit {
  initialTabSelection = 0;

  constructor(
    private readonly navigationService: ManageUpcomingAndHistoricalPaymentsNavigationService,
    private readonly route: ActivatedRoute,
  ) {}

  ngOnInit() {
    this.initialTabSelection = this.navigationService.mapRouteToTabIndex();
  }

  onTabSelect(tabIndex: 0 | 1): void {
    if (tabIndex === 0) {
      this.navigationService.navigateToUpcoming({ relativeTo: this.route });
    } else {
      this.navigationService.navigateToHistory({ relativeTo: this.route });
    }
  }
}
